.guarantee {
  position: relative;
  overflow: hidden;
  padding: 98px 0;
  background-image: url("../img/guarantee_bg.png");
  background-position: center right;
  background-size: cover;
  background-repeat: no-repeat;

  &::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: -1;
    width: 840px;
    height: 630px;
    background-image: url("../img/guarantee_decor.png");
    background-repeat: no-repeat;
    transform: translateX(50%);

    @media (max-width: 475px) {
      width: 650px;
      height: 440px;
      background-size: contain;
    }
  }

  &__title {
    margin-bottom: 80px;
    text-align: left;

    @media (max-width: 768px) {
      margin-bottom: 40px;
    }
  }

  &__text {
    position: relative;
    padding: 20px;
    padding-right: 40px;
    padding-left: 30px;
    font-weight: 400;
    font-size: 22px;
    line-height: 130%;
    color: #f2f1f1;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 1px;
      height: 72px;
      background-color: #e31a40;
      transform: translate(-50%, -70%);

      @media (max-width: 1024px) {
        bottom: -50px;
      }

      @media (max-width: 768px) {
        display: none;
      }
    }

    span {
      font-weight: 700;
    }

    @media (max-width: 768px) {
      grid-column: span 2;
      grid-row: 1;
    }

    @media (max-width: 475px) {
      margin-bottom: 15px;
      padding: 0;
      font-size: 16px;
    }

    @media (max-width: 475px) {
      grid-column: initial;
    }
  }

  &__empty {
    position: relative;
    padding: 0;

    &-image {
      position: absolute;
      right: -30px;
      bottom: -30px;
      width: 530px;
      height: 440px;

      @media (max-width: 1024px) {
        left: -50px;
        right: auto;
        width: 440px;
        height: 360px;
      }
    }

    @media (max-width: 1024px) {
      position: static;
    }

    @media (max-width: 768px) {
      display: none;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 70px;

    &-item {
      border-radius: 5px;
      padding: 30px 35px 35px 85px;

      &-icon {
        margin-bottom: 15px;

        @media (max-width: 768px) {
          width: 40px;
          height: 40px;
        }
      }

      &-title {
        margin-bottom: 10px;
        font-family: "BebasNeue", sans-serif;
        font-weight: 700;
        font-size: 26px;
        line-height: 130%;

        @media (max-width: 768px) {
          font-size: 20px;
        }
      }

      &-text {
        font-weight: 400;
        font-size: 22px;
        line-height: 130%;

        @media (max-width: 768px) {
          font-size: 16px;
        }
      }

      &--white {
        background: linear-gradient(226deg, #d6d5d5 0%, #fff 27.81%, #fff 58.3%, #d6d4d4 100%);

        .guarantee__grid-item-title {
          color: #2d2c2c;
        }

        .guarantee__grid-item-text {
          color: #2d2c2c;
        }
      }

      &--red {
        background: linear-gradient(202deg, #960b1b 0.89%, #ca182c 41.17%, #da3b4d 51.72%, #960b1b 99.17%);

        .guarantee__grid-item-title {
          color: #fff;
        }

        .guarantee__grid-item-text {
          color: #fff;
        }
      }

      &--transparent {
        background-color: transparent;

        .guarantee__grid-item-title {
          color: #fff;
        }

        .guarantee__grid-item-text {
          color: #fff;
        }
      }

      @media (max-width: 1024px) {
        padding: 20px 20px 20px 60px;
      }

      @media (max-width: 768px) {
        padding: 20px;
      }
    }

    @media (max-width: 1024px) {
      position: relative;
    }

    @media (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 475px) {
      grid-template-columns: none;
    }
  }

  &__button-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__button {
    button {
      padding-right: 80px;
      padding-left: 80px;

      @media (max-width: 1024px) {
        padding-right: 60px;
        padding-left: 60px;
      }
    }
  }

  @media (max-width: 475px) {
    padding: 50px 0;
  }
}
