.possibilities {
  margin-bottom: 90px;
  background-image: url("../img/possibilities_bg.png");
  background-position: bottom center;
  background-size: contain;
  background-repeat: no-repeat;

  &__content-wrapper {
    border-radius: 5px;
    padding: 70px 90px;
    background: linear-gradient(226deg, #d6d5d5 0%, #fff 27.81%, #fff 58.3%, #d6d4d4 100%);

    @media (max-width: 768px) {
      padding: 30px 40px;
    }

    @media (max-width: 600px) {
      padding: 20px;
    }
  }

  &__title {
    margin-bottom: 10px;
    color: #2d2c2c;
  }

  &__subtitle {
    margin: 0 auto;
    margin-bottom: 10px;
    max-width: 80%;
    font-family: "BebasNeue", sans-serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 145.5%;
    letter-spacing: 1.08px;
    text-align: center;
    color: #2d2c2c;

    @media (max-width: 600px) {
      max-width: none;
      font-size: 30px;
    }
  }

  &__small-text {
    margin-bottom: 46px;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    text-align: center;
    color: #454545;

    span {
      font-weight: 700;
      color: #ce0000;
    }

    @media (max-width: 600px) {
      font-size: 18px;
    }
  }

  &__cards {
    display: grid;
    grid-auto-rows: 223px;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 58px;
    gap: 25px;

    @media (max-width: 768px) {
      grid-template: none;
    }
  }

  &__card {
    $cards: first, second, third, fourth, fifth, sixth;

    border-radius: 2px;
    padding: 46px 120px 46px 76px;
    background-size: cover;
    background-repeat: no-repeat;

    &-text {
      position: relative;
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
      color: #ededed;

      &::before {
        content: "";
        position: absolute;
        left: -20px;
        top: 13%;
        border-radius: 50%;
        width: 4px;
        height: 4px;
        background-color: #ce0000;
      }

      span {
        font-weight: 700;
      }

      @media (max-width: 600px) {
        font-size: 16px;
      }
    }

    @media (max-width: 1024px) {
      padding: 46px 50px;
      background-position-x: right;
    }

    @media (max-width: 768px) {
      padding: 40px 120px 40px 50px;
    }

    @each $card in $cards {
      &--#{$card} {
        $i: index($cards, $card);

        background-image: url("../img/possibilities_card-#{$i}.png");
      }
    }
  }

  &__button-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__button {
    &::before {
      display: none;
    }

    .button {
      padding-right: 60px;
      padding-left: 60px;
    }
  }
}
